@import url("https://fonts.googleapis.com/css2?family=Inter:ital,opsz@0,14..32;1,14..32&family=Kanit:wght@300&family=Roboto+Condensed:wght@700&family=Sofia+Sans&family=Ubuntu+Condensed&display=swap");
.inter {
  font-family: "Inter", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

.ubuntu-condensed {
  font-family: "Ubuntu Condensed", serif;
  font-weight: 400;
  font-style: normal;
}

.kanit {
  font-family: "Kanit", serif;
  font-weight: 300;
  font-style: normal;
}

.sofia-sans {
  font-family: "Sofia Sans", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

.roboto-condensed {
  font-family: "Roboto Condensed", serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
}

.underline {
  text-decoration: underline;
}

nav {
  width: 100%;
  height: 50px;
  display: flex;
  background-color: #CED4FA;
  justify-content: center;
}
nav .nav-content {
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
nav .nav-content section {
  width: 60%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
nav .nav-content section input {
  width: 240px;
  height: 30px;
  border: none;
}
nav .nav-content section input, nav .nav-content section input::placeholder {
  font-size: 20px;
}
nav .nav-content section .search-button,
nav .nav-content section .nav-toggle {
  background-color: #000;
  color: #fff;
  height: 30px;
  font-size: 20px;
  cursor: pointer;
}
nav .nav-content section .nav-toggle {
  display: flex;
  align-items: center;
  flex-direction: row;
  padding: 0px 11px;
}
nav .nav-content a {
  text-decoration: none;
  font-size: 30px;
  color: #000;
}

body {
  margin: 0;
  padding: 0;
}

*:focus {
  outline: none;
}

a {
  text-decoration: none;
}

main {
  width: 80%;
  margin-left: 10%;
}

button {
  border: none;
}

main#index {
  margin-bottom: 40px;
}
main#index h1 {
  font-size: 60px;
  font-weight: 700;
  margin: 20px 0 0 0;
}
main#index h2 {
  margin: 20px 0;
  font-size: 36px;
}
main#index .slider {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
main#index .slider .slider-button {
  width: 65px;
  height: 240px;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
main#index .slider .slider-button img {
  width: 65px;
  height: 65px;
}
main#index .slider .slider-button.left img {
  transform: rotate(90deg);
}
main#index .slider .slider-button.right img {
  transform: rotate(270deg);
}
main#index .slider .book-covers {
  width: 85%;
  display: flex;
  justify-content: space-between;
}
main#index .slider .book-covers img {
  width: 150px;
  height: 240px;
  cursor: pointer;
}

main#books-page .books-page_content {
  width: 100%;
  margin-top: 40px;
}
main#books-page .books-page_content .content_main-info {
  width: 100%;
  height: 440px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 40px;
}
main#books-page .books-page_content .content_main-info .books-cover {
  width: 25%;
}
main#books-page .books-page_content .content_main-info .books-cover img {
  width: 100%;
  height: 100%;
}
main#books-page .books-page_content .content_main-info .books-info {
  width: 68%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
main#books-page .books-page_content .content_main-info .books-info_name {
  font-weight: 700;
  font-size: 52px;
  margin: 0;
}
main#books-page .books-page_content .content_main-info .books-info_ratings {
  height: 96px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
main#books-page .books-page_content .content_main-info .books-info_ratings .stars {
  width: 220px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
main#books-page .books-page_content .content_main-info .books-info_ratings .stars .star {
  width: 40px;
  aspect-ratio: 1;
  background: #FFFF00;
  clip-path: polygon(50% 0, 79.3892626146% 90.4508497187%, 2.4471741852% 34.5491502813%, 97.5528258148% 34.5491502813%, 20.6107373854% 90.4508497187%);
  /* or more simple
  clip-path: polygon(50% 0,79% 90%,2% 35%,98% 35%,21% 90%); 
  */
}
main#books-page .books-page_content .content_main-info .books-info_author, main#books-page .books-page_content .content_main-info .books-info_genre, main#books-page .books-page_content .content_main-info .books-info .rating-amount {
  font-size: 24px;
  margin: 0;
}
main#books-page .books-page_content .content_main-info .books-info_author span, main#books-page .books-page_content .content_main-info .books-info_genre span, main#books-page .books-page_content .content_main-info .books-info .rating-amount span {
  font-size: 36px;
}
main#books-page .books-page_content .content_main-info .books-info_link button {
  width: inherit;
  background-color: #CED4FA;
  border-radius: 5px;
  padding: 5px 50px;
  font-size: 28px;
  cursor: pointer;
}
main#books-page .books-page_content .content_description {
  width: 100%;
  font-size: 24px;
}
main#books-page .books-page_content .content_description span {
  font-size: 36px;
}
main#books-page .books-page_comment-section {
  background-color: #CED4FA;
  display: flex;
  flex-direction: column;
  padding: 40px;
  margin: 40px 0;
  border-radius: 50px;
}
main#books-page .books-page_comment-section .header {
  font-size: 40px;
  text-align: center;
  margin: 0px;
}
main#books-page .books-page_comment-section .comment-amount {
  opacity: 0.5;
  margin: 12px 0 0 0;
}
main#books-page .books-page_comment-section hr {
  width: 100%;
  height: 2px;
  background-color: #fff;
  border: none;
  z-index: 9999;
}
main#books-page .books-page_comment-section .account-icon {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #9F76FF;
  font-size: 30px;
}
main#books-page .books-page_comment-section .add-your-comment {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 8px 0;
}
main#books-page .books-page_comment-section .add-your-comment .comment-info {
  width: 92%;
}
main#books-page .books-page_comment-section .add-your-comment .comment-info_text {
  margin-bottom: 10px;
}
main#books-page .books-page_comment-section .add-your-comment .comment-info_text input {
  width: calc(100% - 20px);
  height: 60px;
  border: none;
  border-radius: 12px;
  padding-left: 20px;
  font-size: 24px;
  line-height: 24px;
  background-color: #87CBFF;
}
main#books-page .books-page_comment-section .add-your-comment .comment-info_text input::placeholder {
  font-family: "Ubuntu Condensed", serif;
  font-weight: 400;
  font-style: normal;
}
main#books-page .books-page_comment-section .add-your-comment .comment-info_text button {
  position: absolute;
  left: calc(74.3% - 20px);
  margin-top: -54px;
  width: 200px;
  height: 44px;
  border-radius: 50px;
  background-color: #9F76FF;
  color: #fff;
  font-size: 32px;
  line-height: 32px;
  cursor: pointer;
}
main#books-page .books-page_comment-section .add-your-comment .comment-info .login-link {
  color: #fff;
  font-size: 24px;
}
main#books-page .books-page_comment-section .random-comment {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
main#books-page .books-page_comment-section .random-comment .comment-info {
  width: 93%;
}
main#books-page .books-page_comment-section .random-comment .comment-info_details {
  height: 60px;
}
main#books-page .books-page_comment-section .random-comment .comment-info_details h3 {
  margin: 0;
}
main#books-page .books-page_comment-section .random-comment .comment-info_details .random-username {
  color: #9F76FF;
}
main#books-page .books-page_comment-section .random-comment .comment-info_details .comment-date {
  color: #fff;
}
main#books-page .books-page_comment-section .random-comment .comment-info_text {
  font-size: 20px;
}

main#chapter .chapter_book-name {
  font-size: 40px;
  text-align: center;
  margin-top: 40px;
  margin-bottom: 10px;
}
main#chapter .chapter_chapter-select {
  width: 84px;
  height: 36px;
  padding: 0 10px;
  border-radius: 20px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #000;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
}
main#chapter .chapter_chapter-select img {
  width: 24px;
}
main#chapter .chapter_content-separator {
  text-align: center;
  display: table;
  width: fit-content;
  margin: 15px auto;
  font-size: 24px;
}
main#chapter .chapter_chapter-switch {
  width: 275px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
main#chapter .chapter_chapter-switch .chapter-switcher {
  width: 100px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
main#chapter .chapter_chapter-switch .chapter-switcher .switch-arrow {
  height: 20px;
  width: 20px;
}
main#chapter .chapter_chapter-comment,
main#chapter .chapter_chapter-report,
main#chapter .chapter-switcher {
  padding: 10px;
  background-color: #000;
  color: #fff;
}
main#chapter .chapter_chapter-comment,
main#chapter .chapter_chapter-report {
  width: fit-content;
  display: flex;
  flex-direction: column;
  margin: 15px auto;
}
main#chapter .chapter_chapter-report {
  margin-bottom: 30px;
}